import {
    Banner,
    Card,
    DisplayText,
    Form,
    FormLayout,
    Layout,
    Page,
    PageActions,
    TextField,
    Toast,
    RangeSlider
  } from '@shopify/polaris';
  import gql from 'graphql-tag';
  import { Mutation } from 'react-apollo';
  import store from 'store-js';
  
  const UPDATE_PRICE = gql`
    mutation productVariantUpdate($input: ProductVariantInput!) {
      productVariantUpdate(input: $input) {
        userErrors {
          field
          message
        }
        product {
          title
        }
        productVariant {
          id
          price
        }
      }
    }
  `;



  class EditProduct extends React.Component {
    state = {
      interest: 10,
      period: 4,
      frequency: 2,
      price: '',
      variantId: '',
      sliderRate: 1,
      sliderPeriod: 1,
      sliderFrequency: 1,
      showToast: false,
    };
  
    componentDidMount() {
      this.setState({ discount: this.itemToBeConsumed() });
    }
  
    render() {
      const { name, price, discount, variantId } = this.state;

      const suffixStyles = {
        minWidth: '24px',
        textAlign: 'right',
      };
  
      return (
        <Mutation
          mutation={UPDATE_PRICE}
        >
          {(handleSubmit, { error, data }) => {
            const showError = error && (
              <Banner status="critical">{error.message}</Banner>
            );
            const showToast = data && data.productVariantUpdate && (
              <Toast
                content="Sucessfully updated"
                onDismiss={() => this.setState({ showToast: false })}
              />
            );

            return (
              <Page>
                <Layout>
                  {showToast}
                  <Layout.Section>
                    {showError}
                  </Layout.Section>
                  <Layout.Section>
                    <DisplayText size="large">{name}</DisplayText>
                    <Form>
                      <Card sectioned>
                        <FormLayout>
                          <FormLayout.Group>
                            <TextField
                              prefix="$"
                              value={price}
                              disabled={true}
                              label="Original price"
                              type="price"
                            />
                            <TextField
                              prefix="$"
                              value={discount}
                              onChange={this.handleChange('discount')}
                              label="Discounted price"
                              type="discount"
                            />
                          </FormLayout.Group>
                          <RangeSlider
                            label="Interest rate"
                            min={0}
                            max={10}
                            step={0.1}
                            value={this.state.sliderRate}
                            onChange={this.handleSliderRateChange}
                            prefix={<p>0%</p>}
                            suffix={<p style={suffixStyles}>{this.state.sliderRate}%</p>}
                          />
                          <RangeSlider
                            label="Payback period"
                            min={0}
                            max={10}
                            step={1}
                            value={this.state.sliderPeriod}
                            onChange={this.handleSliderPeriodChange}
                            prefix={<p>1 year</p>}
                            suffix={<p style={suffixStyles}>{this.state.sliderPeriod}  years</p>}
                          />
                          <RangeSlider
                            label="Payment frequency"
                            min={0}
                            max={10}
                            step={1}
                            value={this.state.sliderFrequency}
                            onChange={this.handleSliderFrequencyChange}
                            prefix={<p>1</p>}
                            suffix={<p style={suffixStyles}>{this.state.sliderFrequency}/year</p>}
                          />
                        </FormLayout>
                      </Card>
                      <PageActions
                        primaryAction={[
                          {
                            content: 'Save',
                            onAction: () => {
                              const productVariableInput = {
                                id: variantId,
                                price: discount
                              };
                              handleSubmit({
                                variables: { input: productVariableInput },
                              });
                            },
                          },
                        ]}
                        secondaryActions={[
                          {
                            content: 'Remove discount'
                          },
                        ]}
                      />
                    </Form>
                  </Layout.Section>
                </Layout>
              </Page>
            );
          }}
        </Mutation>
      );
    }
  
    handleChange = (field) => {
      return (value) => this.setState({ [field]: value });
    };

    handleSliderRateChange = (sliderRate) => {
        this.itemToBeConsumed();
        this.setState({sliderRate});
    };

    handleSliderPeriodChange = (sliderPeriod) => {
        this.itemToBeConsumed();
        this.setState({sliderPeriod});
    };

    handleSliderFrequencyChange = (sliderFrequency) => {
        this.itemToBeConsumed();
        this.setState({sliderFrequency});
    };
  
    itemToBeConsumed = () => {
      const item = store.get('item');
      const price = item.variants.edges[0].node.price;
      const variantId = item.variants.edges[0].node.id;
      const r = (this.state.sliderRate/this.state.frequency)/100
      console.log(r)
      const n = this.state.frequency*this.state.period
      console.log(n)
      const discounter = (r*price)/(1-(Math.pow(1 + r, -n)))
      console.log(discounter)
      this.setState({ price, variantId });
  
      return (price - discounter).toFixed(2);
    };
  }
  
  export default EditProduct;